//some of these base styles were adapted from a css grid lesson from a wes bos course:
//http://cssgrid.io

//color variables

:root {
  --yellow: #ffffbf;
  --black: #272727;
  --green: #bfffbf;
  --pink: #ffbfff;
  --purple: #bfbfff;
  --blue: #bfffff;
  --bright_red: #f93d66;
  --state_red: #fb87a1;
  --bright_purple: #6d47d9;
  --city_yellow: #ffc800;
  --ibu_red: #b44663;
  --abv_blue: steelblue;
  --beer_brown: #C06018;
  --gray: #ededed;
  --off_white: #f8f8ff;

}

html {
  /* border-box box model allows us to add padding and border to our elements without increasing their size */
  box-sizing: border-box;
  /* A system font stack so things load nice and quick! */
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica,
    Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-weight: 900;
  font-size: 10px;
  color: var(--black);
  text-shadow: 0 2px 0 rgba(0, 0, 0, 0.07);
}

/*
  WAT IS THIS?!
  We inherit box-sizing: border-box; from our <html> selector
  Apparently this is a bit better than applying box-sizing: border-box; directly to the * selector
*/
*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  min-height: calc(100vh - 100px);
  margin: 50px;
  background-attachment: fixed;
  letter-spacing: -.5px;
  overflow-x: hidden;
  background: linear-gradient(#fff, var(--city_yellow), var(--beer_brown));
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0 0 5px 0;
}

/* Each item in our grid will contain numbers */
.item {
  /* We center the contents of these items. You can also do this with flexbox too! */
  display: grid;
  justify-content: center;
  align-items: center;
  border: 5px solid rgba(0, 0, 0, 0.01);
  border-radius: 3px;
  font-size: 35px;
  background-color: var(--yellow); /* best colour */
}

.item p {
  margin: 0 0 5px 0;
}

path {
  cursor: pointer;
}

//this was the only way I could turn the circles back to yellow when a state was hovered over
//i tried to get it to work in D3 but was having problems

path:hover ~ .city {
  fill: var(--city_yellow) !important;
}





